import React from 'react'
import { Portal } from './Portal'

export default class Modal extends React.Component {
  constructor() {
    super(...arguments)
    this.state = { opened: false }
  }
  componentDidMount() {}

  render() {
    return (
      <React.Fragment>
        <Portal selector="#modal">
          <div className="overlay" />
        </Portal>
      </React.Fragment>
    )
  }
}



